:root {
    --font: rgb(50, 15, 107);
}

.scheduled {
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: poppins;
    color: var(--font);
}
.scheduled h1 {
    font-size: 40px;
    margin-top: 30px;
    font-weight: 700;
}
.flex {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 80vw; padding: 0vw 10vw;
    flex-wrap: wrap; gap: 25px;
    margin-top: 25px;
}
.item {
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(10px);
    border-radius: 5px;
    align-items: center;
    text-align: center;
    display: flex;
    flex-direction: row; width: 320px; height: 200px;
}
.item img {
    border-radius: 5px;
}
.item span {
    text-align: left;
    width: 80%;
    padding: 0 10%;
    margin-top: 10px;
}
.timings {
    font-family:poppins; display: flex; flex-direction: column;
    margin-left: 10px; width: 150px; text-align: left;
}

.btns {
    height: 50px;
}
.btns button {
    background: transparent; border: none;
    color: black; font-weight: 200;
    outline: none; font-size: 20px; font-family: poppins;
}
.bold {
    font-weight: 600;
    margin-left: -10px;
}


.lds-defaultThree {
    position: relative; transform: scale(2); margin-left: -150px;
    margin-top: 50px;
}

.lds-defaultThree div {
position: absolute;
width: 6px;
height: 6px;
background: var(--font);
border-radius: 50%;
animation: lds-default 1.2s linear infinite;
transform: scale(3);
}
.lds-defaultThree div:nth-child(1) {
animation-delay: 0s;
top: 37px;
left: 66px;
}
.lds-defaultThree div:nth-child(2) {
animation-delay: -0.1s;
top: 22px;
left: 62px;
}
.lds-defaultThree div:nth-child(3) {
animation-delay: -0.2s;
top: 11px;
left: 52px;
}
.lds-defaultThree div:nth-child(4) {
animation-delay: -0.3s;
top: 7px;
left: 37px;
}
.lds-defaultThree div:nth-child(5) {
animation-delay: -0.4s;
top: 11px;
left: 22px;
}
.lds-defaultThree div:nth-child(6) {
animation-delay: -0.5s;
top: 22px;
left: 11px;
}
.lds-defaultThree div:nth-child(7) {
animation-delay: -0.6s;
top: 37px;
left: 7px;
}
.lds-defaultThree div:nth-child(8) {
animation-delay: -0.7s;
top: 52px;
left: 11px;
}
.lds-defaultThree div:nth-child(9) {
animation-delay: -0.8s;
top: 62px;
left: 22px;
}
.lds-defaultThree div:nth-child(10) {
animation-delay: -0.9s;
top: 66px;
left: 37px;
}
.lds-defaultThree div:nth-child(11) {
animation-delay: -1s;
top: 62px;
left: 52px;
}
.lds-defaultThree div:nth-child(12) {
animation-delay: -1.1s;
top: 52px;
left: 62px;
}
@keyframes lds-default {
0%, 20%, 80%, 100% {
  transform: scale(1);
}
50% {
  transform: scale(1.5);
}
}