:root {
    --font: rgb(50, 15, 107);
}
.formHeaders {
    font-family: poppins;
    color: var(--font);
    margin-top: 20px;
    margin-bottom: 10px;
    text-align:center;
}
.formHeaders h2 {
    font-weight: 300;
}
.formFlex {
    font-family: poppins;
    color: var(--font);
    overflow: scroll;
}
.formFlex h1 {
    font-weight: 600;
}
.formFlex h2 {
    font-weight: 300;
}
.inputForm {
    width: 500px;
    height: 500px;
    justify-content: center;
    display: flex;
    flex-direction: column;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    margin: 0 auto;
    gap: 10px;
    align-items: center;
}
.inputForm label {
    font-weight: 600; font-size: 14px; text-transform: capitalize;

}
#date {
    width:250px; padding: 5px; font-family: poppins; color: var(--font); border-radius: 5px; border: 1px solid var(--font);
}
#time {
    width:250px; padding: 5px; font-family: poppins; color: var(--font); border-radius: 5px; border: 1px solid var(--font);
}
#message {
    width:250px; padding: 5px; font-family: poppins; color: var(--font); border-radius: 5px; border: 1px solid var(--font);
}
.clearImg {
    background: transparent; border: none; outline: none;
    color: black; font-weight: 200; margin-top: 10px;
}
.fileBox {
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex; flex-direction: column;
    align-items: center; justify-content: center;
}
.formFlex {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 95vh;
    width: 100vw;
}
.uploadBtn {
    width: 200px;
    height: 45px;
    line-height: 45px;
    background: black;
    color: white;
    font-weight: 200;
    border-radius: 2.5px;
    text-align: center;
}
.btnSubmit {
    width: 150px;
    height: 45px;
    line-height: 45px;
    background: black;
    color: white;
    font-weight: 200;
    border-radius: 2.5px;
    text-align: center;
    border: none; outline: none;
}